export function weekdayStr(v: number) {
  switch (v) {
    case -1:
      return "토요일";
    case 0:
      return "일요일";
    case 1:
      return "월요일";
    case 2:
      return "화요일";
    case 3:
      return "수요일";
    case 4:
      return "목요일";
    case 5:
      return "금요일";
    case 6:
      return "토요일";
    default:
      return "";
  }
}

export function durationToString(d: number) {
  const hour = Math.floor(d / (60 * 60));
  const minute = (d / 60) % 60;
  let message = "";
  if (hour > 0) message = `${hour} 시간 `;
  if (minute > 0) message += `${minute} 분`;
  return message;
}
